@import "_vars";
@import "_mixins";
@import "_fonts";

html {
  font-size: 10px;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;

  font-family: "TT Norms", sans-serif;
  font-size: 16px;
  color: $black-light;

  background-color: $white;

  position: relative;
}

img {
  max-width: 100%;
}

a {
  display: inline-block;

  text-decoration: none;
  color: $white;
}

ul {
  list-style: none;
  padding: 0;
}

// .site-container {
//   overflow: hidden;
// }

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1410px;
}
.container-fluid {
  max-width: 100%;
  margin: 0 auto;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

h1 {
  font-size: 5.4rem;
  line-height: 1.5;
  font-weight: 800;
  color: $white;
}
h2 {
  font-size: 4.6rem;
  line-height: 1.5;
  font-weight: 700;
  color: $black;
}
h4 {
  font-size: 2.2rem;
  line-height: 1.5;
  font-weight: 700;
  color: $black;
}
h5 {
   font-size: 1.8rem;
   font-weight: 500;
   line-height: 1.5;
}
section {
   margin-top: 12rem;
}
.section__header {
   display: flex;
   align-items: center;
   margin-bottom: 5rem;

   &-num {
      margin: 0 2.5rem;
      color: $blue;
      font-weight: 700;
   }
}


/* Logo */
.logo {
}
.logo__img {
  max-width: 9rem;
}
/* Menu */
.menu-list {
  display: flex;
  margin: 0;

  &__item {
    position: relative;
    padding: 1.6rem;

    &:hover {
      .menu-list__link,
      .menu-list__icon {
        color: $blue;
        fill: $blue;
      }
      .menu-sublist {
        display: block;
      }
    }
  }
  &__icon {
    margin-left: 1rem;
    width: 1.6rem;
    height: 1.6rem;

    fill: $black;
    transition: all 0.1s linear;
  }
  &__link {
    display: flex;
    transition: all 0.1s linear;
  }
}
.menu-sublist {
  display: none;
  padding: 2.5rem;
  max-width: 90rem;

  background-color: $black-bg;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);

  &__item {
    padding: 0.5rem;
    width: 100%;
  }
  &__link {
    font-size: 18px;
    color: $white;

    transition: all .1s linear;
    &:hover {
      color: $blue;
    }
  }
}
